import React, { Component } from "react";
import { InputGroup, Popover, Switch, Anchor } from "common";
import { SubscribeContext } from "common/module/subscribe";
class Config extends Component {

 getName(url) {
  let name = `dhruv-techapps.github.io`;
  if (url && url.match("://.*") !== null) {
   name = url.split("/")[2];
  } else if (url) {
   name = url;
  }
  return name;
 }

 render() {
  const { onChange, config, mode } = this.props;
  const { enable, url, startTime, name } = config;
  return <div id="config" className="card mb-4 p-3">
   <div className="d-flex align-items-center justify-content-between">
    <Anchor className="mr-4 flex-grow-1 ml-1 mb-0 h4 font-weight-normal" href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/configuration">Configuration</Anchor>
    <Switch name="enable" checked={enable} onChange={onChange}>
     <Popover dataTitle="Enable" dataContent="To enable/disable configuration&lt;br>&lt;br> Disable configuration if you dont want to execute below actions." />
    </Switch>
   </div>
   {enable && <>
    <div className="row mt-3">
     <div className="col-12 mb-2">
      <InputGroup prop="url" name="url" placeholder="https://dhruv-techapps.github.io/" prepend="URL" value={url} onChange={onChange} />
     </div>
     <div className="col-12 col-md-6 mb-2">
      <InputGroup prop="Name" name="name" placeholder={this.getName(url)} prepend="Name" value={name} onChange={onChange} />
     </div>
     <div className="col-12 col-md-6">
      <InputGroup value={startTime} disabled={!this.context.subscription || mode !== "PRO"} name="startTime" onChange={onChange} placeholder="hh:mm:ss" prepend="Start Time" />
     </div>
    </div>
   </>}
  </div>;
 }
}
Config.contextType = SubscribeContext;
export default Config;