import React, { Component } from "react";
import SubscribeContext from "./SubscribeContext";
import { PaymentService } from "common/services";
import { Modal } from "common/modal";
import { Anchor } from "common";
class SubscribeModal extends Component {

 constructor() {
  super();
  this.state = { error: '', message: '', purchases: [] };
  PaymentService.getPurchases().then(result => {
   if (result.state === "FOUND") {
    this.context.updateSubscription(true);
    this.setState({ purchases: result.purchases });
   } else {
    PaymentService.subscription().then(result => {
     if (result) {
      this.context.updateSubscription(true);
     }
    });
   }
  }).catch(error => {
   this.setState({ error, message: '' });
  });

  PaymentService.skuDetails().then(result => {
   if (result.state === "FOUND") {
    this.inAppProducts = result.inAppProducts;
   }
  });
 }

 getManualRegistration() {
  PaymentService.getManualRegistration().then(result => {
   if (result.state === "ACTIVE") {
    this.setState({ message: "Subscription activated successfully!" });
    this.context.updateSubscription(true);
   } else if (result.state === "NOT_FOUND") {
    this.setState({ message: result.message, error: '' });
   }
  }).catch(error => {
   this.setState({ error, message: '' });
  });
 }

 buy() {
  PaymentService.buy().then(result => {
   this.context.updateSubscription(true);
   let { purchases } = this.state;
   if (this.inAppProducts) {
    purchases.push(this.inAppProducts.filter(inAppProduct => inAppProduct.sku === result.sku)[0]);
   } else {
    this.setState({ error: "In-App Products not found", message: '' });
   }
   this.setState({ purchases, message: "Subscription activated successfully!", error: '' });
  }).catch(error => {
   this.setState({ error, message: '' });
  });
 }

 consumePurchase(sku) {
  PaymentService.consumePurchase(sku).then(result => {
   let { purchases } = this.state;
   purchases = purchases.filter(purchase => purchase.sku === sku);
   this.setState({ purchases, message: "Subscription deactivated successfully!", error: '' });
  }).catch(error => {
   this.setState({ error, message: '' });
  });
 }

 reload() {
  window.location.reload()
 }

 render() {

  const { error, message, purchases } = this.state;
  const buy = this.buy.bind(this);
  const consumePurchase = this.consumePurchase.bind(this);
  const getManualRegistration = this.getManualRegistration.bind(this);

  const footer = error || message ? <div className="d-flex">
   <small className="text-danger mr-5">{error}</small>
   <small className="text-success mr-5">{message}</small>
  </div> : undefined;
  return (
   <Modal id="subscribe-modal" title="Subscription" className="modal-lg" footer={footer}>
    <div>
     <h4 className="mt-1">PRO Features</h4>
     <ul>
      <li><Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/configuration">Configuration</Anchor>
       <ul><li><Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/configuration#start-time">Start Time</Anchor></li></ul>
      </li>
      <li><Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/batch">Batch</Anchor>
       <ul>
        <li><Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/batch#init-wait">Init Wait</Anchor></li>
        <li><Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/batch#repeat">Batch Repeat </Anchor></li>
        <li><Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/batch#repeat-interval">Batch Repeat Interval</Anchor></li>
       </ul>
      </li>
      <li><Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/action">Action</Anchor>
       <ul>
        <li><Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/action#init-wait">Init Wait</Anchor></li>
        <li><Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/action#repeat">Actio Repeat</Anchor></li>
        <li><Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/action#repeat-interval">Actio Repeat Interval</Anchor></li>
       </ul>
      </li>
     </ul>
     {purchases.length === 0 && (this.context.subscription ?
      <div>
       <hr />
       <p className="text-muted mb-0">You are subscribed to PRO features</p>
      </div> :
      <>
       <hr />
       <div className="d-flex justify-content-between flex-column align-items-center">
        <p className="text-muted">Subscribe to PRO features with 15 days free trial</p>
        <button className="btn btn-primary btn-block" style={{ whiteSpace: "nowrap" }} onClick={buy}>Subscribe</button>
       </div>
      </>
     )}
     {purchases.length !== 0 && <>
      <hr />
      <div className="d-flex justify-content-between flex-column align-items-center">
       <p className="text-muted">You are subscribed to PRO features</p>
       <button className="btn btn-outline-secondary btn-block" onClick={() => { consumePurchase(purchases[0].sku); }}>Cancel Subscription</button>
      </div>
     </>}
     {!this.context.subscription && <div>
      <hr />
      <div className="d-flex justify-content-center flex-column align-items-center">
       <p className="text-muted">If you have made payment through paypal and got manual registration from admin please click Check Registraion button</p>
       <button className="btn btn-outline-secondary btn-block" onClick={getManualRegistration} style={{ whiteSpace: "nowrap" }}>Check Registraion</button>
      </div>
     </div>}
     {((purchases.length !== 0 && this.context.subscription) || (purchases.length === 0 && !this.context.subscription)) && <>
      <hr />
      <small className="text-danger font-weight-normal d-block mt-3 mb-2">
       * If you have already subscribed and dont see updated status <a href="#reload" onClick={this.reload}>Refresh</a> browser page<br />
       * Please check <a href="https://pay.google.com/payments/u/0/home#subscriptionsAndServices" rel="noopener noreferrer" target="_blank">Google Payment Center</a> for subscription
      </small>
     </>}
    </div>
   </Modal>
  );
 }
}
SubscribeModal.contextType = SubscribeContext;
export default SubscribeModal;
