import React, { Component } from 'react';
import { SubscribeProvider } from './common/module/subscribe';
import Configs from 'app/Configs';
import Header from 'app/Header';
import Footer from 'app/Footer';
import { StorageService } from 'common/services';

const LOCAL_STORAGE_MODE = "mode";

class App extends Component {

 constructor() {
  super();
  this.state = { mode: "LITE" };
  this.init();
 }

 init() {
  StorageService.getItem(LOCAL_STORAGE_MODE, "LITE").then(mode => {
   this.setState({ mode });
  });
 }

 componentDidMount() {
  window.$('[data-toggle="popover"]').popover();
 }

 componentDidUpdate() {
  window.$('[data-toggle="popover"]').popover();
 }

 onModeChange(e) {
  let { name, checked } = e.currentTarget;
  let mode = checked ? 'PRO' : 'LITE';
  this.setState({ [name]: mode }, _ => StorageService.setItem(LOCAL_STORAGE_MODE, mode));
 }

 render() {
  const { mode } = this.state;
  const onModeChange = this.onModeChange.bind(this);
  return <SubscribeProvider>
   <Header mode={mode} onChange={onModeChange} />
   <Configs mode={mode} />
   <Footer />
  </SubscribeProvider>;
 }
}
export default App;