import React, { Component } from "react";
import Main from "./Main";
import AddonModal from "./main/action/addon/AddonModal";
import { Select, Loading } from "common";
import { getConfigName } from "./Util";
import { StorageService } from "common/services";

const LOCAL_STORAGE_CONFIGS = "configs";
const defaultAddon = { xpath: "", value: "", condition: "" };
const defaultAction = { initWait: "", xpath: "", value: "", repeat: "", repeatInterval: "" };
const defaultConfig = { enable: true, url: "", name: "", refresh: false, repeat: "", repeatInterval: "", initWait: "", startTime: "" };
class Configs extends Component {

 constructor() {
  super();
  this.state = { configs: undefined, loading: true, addon: this.getAddon(), selectedConfigIndex: -1, selectedActionIndex: -1 };
  this.init();
 }

 init() {
  StorageService.getItem(LOCAL_STORAGE_CONFIGS, [this.getConfig()]).then(configs => {
   let selectedConfigIndex = this.checkQueryParams(configs);
   this.setState({ configs, loading: false, addon: this.getAddon(), selectedConfigIndex });
  })
 }

 getConfig() {
  let config = Object.assign({}, defaultConfig);
  config.actions = [];
  config.actions[0] = this.getAction();
  return config;
 }

 getAction() {
  let action = Object.assign({}, defaultAction);
  action.addon = this.getAddon();
  return action;
 }

 getAddon() {
  return Object.assign({}, defaultAddon);
 }

 checkQueryParams(configs) {
  try {
   let object = {};
   let selectedConfigIndex = 0;
   if (window.location.search) {
    let params = window.location.search.replace("?", "").split("&");
    for (let index in params) {
     let [name, value] = params[index].split("=");
     object[name] = decodeURIComponent(value)
    }
    if (object.url) {
     selectedConfigIndex = configs.findIndex(config => config.url === object.url);
     if (selectedConfigIndex === -1 && object.xpath) {
      let config = this.getConfig();
      config.url = object.url;
      config.actions[0].xpath = object.xpath;
      configs.push(config);
      selectedConfigIndex = configs.length - 1;
     } else if (object.xpath) {
      let XPathIndex = configs[selectedConfigIndex].actions.findIndex(action => action.xpath === object.xpath);
      if (XPathIndex === -1) {
       let action = this.getAction();
       action.xpath = object.xpath;
       configs[selectedConfigIndex].actions.push(action);
      }
     } else if (object.error_xpath) {
      let XPathIndex = configs[selectedConfigIndex].actions.findIndex(action => action.xpath === object.error_xpath);
      if (XPathIndex !== -1) {
       configs[selectedConfigIndex].actions[XPathIndex].error = true;
      }
     }
     StorageService.setItem(LOCAL_STORAGE_CONFIGS, configs);
    }
   }
   return selectedConfigIndex;
  } catch (error) {
   console.error(error);
  }
 }

 updateStorage() {
  StorageService.setItem(LOCAL_STORAGE_CONFIGS, this.state.configs);
 }

 addConfig() {
  let { configs } = this.state;
  configs.push(this.getConfig());
  this.setState({ configs, selectedConfigIndex: configs.length - 1 }, this.updateStorage);
 }

 selectConfig(e) {
  let index = parseInt(e.currentTarget.value);
  this.setState({ selectedConfigIndex: index });
 }

 removeConfig(e) {
  e.stopPropagation();
  let { configs } = this.state;
  let index = parseInt(e.currentTarget.getAttribute("index"));
  configs.splice(index, 1);
  let selectedConfigIndex = configs.length >= 1 ? index === 0 ? index : index - 1 : 0;
  this.setState({ configs, selectedConfigIndex }, this.updateStorage);
 }

 reorderActions(actions) {
  let { configs, selectedConfigIndex } = this.state;
  configs[selectedConfigIndex].actions = actions;
  this.setState({ configs }, this.updateStorage);
 }

 onConfigChange(e) {
  let { name, value, checked, type } = e.currentTarget;
  let { configs, selectedConfigIndex } = this.state;
  configs[selectedConfigIndex][name] = type === "checkbox" ? checked : value;
  this.setState({ configs }, this.updateStorage);
 }

 addAction(e) {
  let { configs, selectedConfigIndex } = this.state;
  configs[selectedConfigIndex].actions.push(this.getAction());
  this.setState({ configs }, this.updateStorage);
 }

 removeAction(e) {
  e.stopPropagation();
  let { configs, selectedConfigIndex } = this.state;
  let index = parseInt(e.currentTarget.getAttribute("index"));
  configs[selectedConfigIndex].actions.splice(index, 1);
  this.setState({ configs }, this.updateStorage);
 }

 onActionChange(e) {
  let { name, value, checked, type } = e.currentTarget;
  let { configs, selectedConfigIndex } = this.state;
  let index = e.currentTarget.getAttribute("index");
  configs[selectedConfigIndex].actions[index][name] = type === "checkbox" ? checked : value;
  if (name === "xpath") {
   delete configs[selectedConfigIndex].actions[index].error;
  }
  this.setState({ configs }, this.updateStorage);
 }

 onAddonChange(e) {
  let { name, value } = e.currentTarget;
  let { configs, selectedConfigIndex, selectedActionIndex } = this.state;
  let action = configs[selectedConfigIndex].actions[selectedActionIndex];
  action.addon[name] = value;
  this.setState({ configs }, this.updateStorage);
 }

 editAddon(e) {
  let index = e.currentTarget.getAttribute("index");
  let { configs, selectedConfigIndex } = this.state;
  let addon = configs[selectedConfigIndex].actions[index].addon || (configs[selectedConfigIndex].actions[index].addon = this.getAddon());
  this.setState({ addon, selectedActionIndex: index });
 }

 render() {
  const { mode } = this.props;
  const { configs, selectedConfigIndex, addon, loading } = this.state;
  if (loading) {
   return <Loading />
  }
  const config = configs[selectedConfigIndex];
  const addConfig = this.addConfig.bind(this);
  const removeConfig = this.removeConfig.bind(this);
  const selectConfig = this.selectConfig.bind(this);
  const onConfigChange = this.onConfigChange.bind(this);
  const onActionChange = this.onActionChange.bind(this);
  const removeAction = this.removeAction.bind(this);
  const addAction = this.addAction.bind(this);
  const editAddon = this.editAddon.bind(this);
  const onAddonChange = this.onAddonChange.bind(this);
  const reorderActions = this.reorderActions.bind(this);

  return <>
   <div className="container mb-4">
    <div className="row mb-4 mt-3">
     <form className="col d-flex form-inline ">
      <Select id="configuration-list" className="mr-md-3 flex-grow-1" onChange={selectConfig} value={selectedConfigIndex} options={configs.map((config, index) => ({ name: config.name || getConfigName(config.url, index), value: index }))} />
      <button className="btn btn-success mt-2 flex-grow-1 flex-md-grow-0 mt-md-0" type="button" onClick={addConfig}>Add New</button>
     </form>
    </div>
    <div className="row">
     <Main config={config} mode={mode} onConfigChange={onConfigChange} reorderActions={reorderActions} onActionChange={onActionChange} removeAction={removeAction} addAction={addAction} editAddon={editAddon} />
    </div>
    <button type="button" disabled={configs.length <= 1} className="btn btn-outline-danger mt-3 btn-block" onClick={removeConfig} index={selectedConfigIndex}>Delete</button>
   </div>
   <AddonModal onChange={onAddonChange} addon={addon} />
  </>
 }
}
export default Configs;