import React, { Component } from "react";
import { Input, Popover } from "common";
import { MinusCircle, Code, Bars } from "common/svg";
import { SubscribeContext } from "common/module/subscribe";
import { getXPathName } from "app/Util";

class ActionBody extends Component {

 render() {
  const { index, remove, editAddon, onChange, action, mode, columns } = this.props;
  const { repeat, name, initWait, repeatInterval, value, xpath, error } = action;
  return <tr draggable="true" id={index} onDragStart={this.props.onDragStart} onDrop={this.props.onDrop} onDragOver={this.props.onDragOver}>
   <td className="align-middle">
    <Bars style={{ cursor: "move" }} />
   </td>
   {columns.findIndex(column => column.name === "Init Wait" && column.value) !== -1 && <td>
    <Input type="float" value={initWait} name="initWait" placeholder="0" onChange={onChange} index={index} disabled={!this.context.subscription || mode !== "PRO"} />
   </td>}
   {(columns.findIndex(column => column.name === "Name" && column.value) !== -1) && <td>
    <Input value={name} name="name" onChange={onChange} index={index} placeholder={getXPathName(xpath, index)} />
   </td>}
   <td>
    <Input value={xpath} name="xpath" placeholder="//element[@id='1']" className={error ? 'is-invalid' : ''} onChange={onChange} index={index} />
   </td>
   <td>
    <Input value={value} name="value" onChange={onChange} index={index} />
   </td>
   {columns.findIndex(column => column.name === "Repeat & R-Interval" && column.value) !== -1 && <><td>
    <Input type="number" value={repeat} name="repeat" placeholder="0" onChange={onChange} index={index} disabled={!this.context.subscription || mode !== "PRO"} />
   </td><td>
     <Input type="float" value={repeatInterval} name="repeatInterval" placeholder="0" onChange={onChange} index={index} disabled={!this.context.subscription || mode !== "PRO"} />
    </td></>}
   <td className="align-middle">
    <Popover dataContent="Add Condition" className="p-0 text-primary" index={index} onClick={editAddon}>
     <Code data-target="#addon-modal" href="#addon" data-toggle="modal" />
    </Popover>
    <MinusCircle onClick={remove} index={index} className="ml-2" />
   </td>
  </tr>;
 }
}
ActionBody.contextType = SubscribeContext;
export default ActionBody;