import React, { Component } from "react";
import { InputGroup, Popover, Switch, Anchor } from "common";
import { SubscribeContext } from "common/module/subscribe";
class Batch extends Component {
 render() {
  const { onChange, config, mode } = this.props;
  const { repeatInterval, repeat, refresh, initWait } = config;
  return <section className="card p-3 mb-4" id="batch">
   <div className="d-flex align-items-center justify-content-between">
    <Anchor className="mr-4 ml-1 h4 mb-0 font-weight-normal" href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/batch">Batch</Anchor>
    <Switch name="refresh" checked={refresh} onChange={onChange} disabled={!this.context.subscription || mode !== "PRO"}>
     <Popover dataTitle="Refresh" dataContent="To refresh page once all actions are completed.&lt;br/> * If your using batch repeat dont use refresh." />
    </Switch>
   </div>
   <form className="form mt-3">
    <fieldset disabled={!this.context.subscription || mode !== "PRO"}>
     <div className="row">
      <div className="col-12 mb-2 col-md border-right">
       <InputGroup type="float" value={initWait} name="initWait" onChange={onChange} placeholder="0" prepend="Init Wait" />
      </div>
      {!config.refresh && <>
       <div className="col-12 mb-2 col-md">
        <InputGroup type="number" value={repeat} name="repeat" disabled={config.refresh} onChange={onChange} placeholder="0" href="https://github.com/Dhruv-Techapps/auto-click/wiki/Repeat" prepend="Repeat" dataTitle="Batch Repeat" prependPopover="You can repeat below actions for more than one time. Its numeric field &lt;hr/>* 1 &lt;br/>* can be empty/blank &lt;br/>* -2 for endless repeat" />
       </div>
       <div className="col-12 col-md">
        <InputGroup type="number" value={repeatInterval} name="repeatInterval" disabled={config.refresh} onChange={onChange} placeholder="0" href="https://github.com/Dhruv-Techapps/auto-click/wiki/Repeat-Interval" prepend="R-Interval" dataTitle="Batch Repeat Interval" append="sec" prependPopover="Its dependent on repeat value &lt;br/> To run batch at specific interval provide time here. Its value are in sec &lt;hr/>* 1 &lt;br/>* 1e5 defines range from 1-5 &lt;br/>* can be empty/blank" />
       </div>
      </>}
     </div>
    </fieldset>
   </form>
  </section>;
 }
}
Batch.contextType = SubscribeContext;
export default Batch;